body {
	background-color: #282c34;
}
.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 75vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}
.add-container {
	min-height: 10vh;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	font-size: calc(10px + 2vmin);
	color: white;
	background-color: #282c34;
	padding: 2rem;
}
.select-filter {
	margin-bottom: 2rem;
	width: 50vw;
	height: 5vh;
	color: white;
	background-color: #282c34;
}
.select-add {
	margin-left: 0.3rem;
	/* width: 5vw; */
	height: 5vh;
	color: white;
	background-color: #282c34;
}
.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
.form-container {
	display: flex;
	flex-direction: column;
	font-size: 1rem;
	/* background: red; */
	width: 18vw;
}
.btn {
	/* background: red */
	width: 5rem;
	height: 2rem;
}

.add-item-input {
	height: 2rem;
}
.add-order-qty {
	width: 4rem;
}
.update-container {
	width: 80vw;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
}
.update-item-container {
	padding: 5px;
	border-radius: 5px;
	border: 2px solid white;
}
.form-container-update {
	padding: 10px;
	border-radius: 5px;
	border: 2px solid white;
}
