ul {
	display: flex;
	list-style-type: none;
	width: 100%;
	background-color: #282c34;
	padding: 2rem;
	justify-content: flex-end;
}

li button {
	color: white;
	background-color: #282c34;
	/* width: 30%; */
	text-decoration: none;
	font-size: 1.5rem;
	padding: 0.5rem;
	border: 1px solid white;
	border-radius: 10px;
	margin-left: 10px;
}
