* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
.container {
	justify-self: start;
}
.item-card {
	float: left;
	border: 1px solid;
	width: 280px auto;
	padding: 10px;
	margin: 2px;
	border-radius: 30px;
	margin: 5px 10px;
	font-size: 1.48rem;
}
.Images {
	width: 45%;
	float: left;
}
.Images img {
	width: 100%;
}
.desc {
	/* background-color: red; */
	width: 55%;
	float: left;
	padding-left: 20px;
	text-align: center;
}
.clearfix::after {
	content: '';
	clear: both;
	display: table;
}
.text {
	padding: 10px;
	width: 100%;
}
.items-container {
	width: 60%;
	/* background-color: #fff; */
	float: left;
}
.cart {
	/* text-align: center; */
	float: left;
	width: 20%;
	border: solid 1px white;
	border-radius: 11px;
}
